<template>
<div class="address">
	<van-nav-bar
		title="新增收货地址"
		left-text="返回"
		left-arrow
		@click-left="onClickLeft"
		/>
    <van-address-edit 
        :area-list="areaList" 
        show-postal
        show-set-default 
        show-search-result
        :search-result="searchResult"
        :address-info="addressInfo" 
        :area-columns-placeholder="['请选择', '请选择', '请选择']"
        @save="onSave"
        />
</div>
</template>

<script>
import "../assets/scss/address.scss";
import area from '../area'
import {
    mapGetters
} from 'vuex' 
import { utils } from '../assets/js/utils'

export default {
    data() {
        return {
            areaList: area,
            searchResult: [],
            addressInfo: {
                name: '',
                tel: '',
                province: '',
                city: '',
                county: '',
                addressDetail: '',
                areaCode: '',
                postalCode: '',
                isDefault: false
            }
        };
	},
    methods: {
		onClickLeft() {
			this.$router.go(-1);
        },
        // onClickRight() {
        //     this.$router.push({
        //         name: 'MyAddress'
        //     })
        // },
        onSave(content) {

            if(!this.getToken) {
                this.$router.push({
                    name: 'Login'
                })
                return ;
            }

            let obj = Object.assign({}, content);

            delete obj.country;

            obj.appkey = this.appkey;
            obj.tokenString = this.getToken;
            obj.isDefault = Number(obj.isDefault);

            let data = utils.queryString(obj);

            this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			});

			this.axios({
				method: 'POST',
				url: '/addAddress',
				data
			})
			.then(res => {
				this.$toast.clear();
				console.log(res);
                this.$toast(res.data.msg);
                this.$router.replace({
					name: 'MyAddress'
				});
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
			})

        }
    },
    computed: {
        ...mapGetters(['getToken'])
    }
};
</script>
